import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        itemarr: [],
        islogin: false,
    },
    mutations: {
        islogint(state) {
            state.islogin = true
        },
        isloginf(state) {
            state.islogin = false
        },
        itemarrpush(state, e) {
            var flag = true
            if (state.itemarr) {

                state.itemarr.some(item => {
                    if (item.id === e.id) {
                        item.cont += e.cont
                        flag = false
                    }
                })
            }
            if (flag) {
                state.itemarr.push(e)
            }
            localStorage.setItem('car', JSON.stringify(state.itemarr))
        },
        ijcli(state, e, i) {
            state.itemarr.splice[i, 1, e]
        },
        cararr(state) {
            state.itemarr = JSON.parse(localStorage.getItem('car'))
        },
        isarrdelete(state, i) {
            state.itemarr.splice(i, 1)
            localStorage.setItem('car', JSON.stringify(state.itemarr))
        }
    },
    actions: {},
    modules: {}
})